import axios from 'axios';

export default {
    methods : {
        async $api(url, data) {
            
            if(process.env.NODE_ENV=="production"){
                url = process.env.VUE_API_SERVER+url;
            }
            console.log("url : ",url);
            return (await axios({
                method : 'post',
                url,
                data
            }).catch(e => {
                console.log(e);
            })).data;
        }
    }
}